#TaxonomyViewer {
  display: grid;
  grid-template-columns: 1fr max-content;
  width: 100%;
}

section.sidebar {
  --border-color: black;
  --item-bg-color: white;
  --shadow-color: grey;
  overflow: auto;
}

section.sidebar .term__list {
  display: grid;
  list-style: none;
}

section.sidebar > .term__list > .term__item:nth-child(even) {
  background-color: var(--shadow-color);
}

section.sidebar .term__item {
  border-radius: 2px;
  margin: 0.5rem;
}

section.sidebar .item__container {
  display: flex;
  align-items: center;
  border: var(--border-color) 1px solid;
  padding: 0.5rem;
  background: var(--item-bg-color);
  border-radius: 5px;
  box-shadow: var(--shadow-color) 1px 1px 5px;
  width: fit-content;
}

section.sidebar .item__container > * {
  margin: 0.5rem;
}

section.sidebar .item__container h3 {
  font-weight: bold;
}




